













































































































































import { FormModel } from '@/models/FormModel';
import { PersonalInformation } from '@/models/CeremonyFormModel';
import Vue from 'vue';

export default Vue.extend({
  props: {
    checkMode: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      personal: new PersonalInformation(),
      informMethods: [
        'textmsg',
        'email',
        'line',
        'wechat',
        'letter',
      ],
    };
  },
  computed: {
    confirmApplication(): boolean {
      return this.$store.state.confirmApplication;
    },
    checkedRepeatList(): boolean {
      return this.$store.state.checkedRepeatList;
    },
    membersList(): FormModel[] {
      return this.$store.state.membersList;
    },
    personalInfo(): PersonalInformation {
      return this.$store.state.form.ceremonyForm.personalInformation;
    },
  },
  watch: {
    personalInfo() {
      this.init();
    },
  },
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.personal = this.personalInfo;
    },
    checkInfo() {
      if (this.confirmApplication === false && this.checkedRepeatList === false) { // 如果沒有套用舊資料 也 還沒檢查過
        const repeatFormList = [] as FormModel[];
        this.membersList.forEach((memberForm: FormModel) => {
          if ((this.personal.name && this.personal.name === memberForm.ceremonyForm.personalInformation.name)
            || (this.personal.address && this.personal.address === memberForm.ceremonyForm.personalInformation.address)
            || (this.personal.telephone && this.personal.telephone === memberForm.ceremonyForm.personalInformation.telephone)
            || (this.personal.phone && this.personal.phone === memberForm.ceremonyForm.personalInformation.phone)
          ) {
            repeatFormList.push(memberForm);
          }
        });

        if (repeatFormList.length > 0) { // 如果有重複的話，動作
          this.$store.commit('updateRepeatList', repeatFormList);
          this.$modal.show('check-info-repeat-modal');
        } else { // 沒有重複的話
          this.goStep('next'); // 就存起來到下一步
        }
      } else { // 如果有套用舊資料 或是 有檢查過
        this.goStep('next'); // 就存起來直接到下一步
      }
    },
    goStep(move: string) {
      if (move === 'next') {
        this.$router.push('longevity');
      }
    },
  },
});
